"use client";
import { useEffect, useState } from "react";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Calendar } from "lucide-react";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";

export default function UpcomingBills() {
  const [bills, setBills] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [form, setForm] = useState({
    title: "",
    category: "",
    date: "",
    amount: "",
  });

  // ✅ Fetch bills from backend
  useEffect(() => {
    fetch("/api/bills")
      .then((res) => res.json())
      .then((data) => setBills(data.reverse())); // sabse naya upar
  }, []);

  // ✅ Add new bill
  const addBill = async () => {
    const res = await fetch("/api/bills", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(form),
    });
    const newBill = await res.json();
    setBills([newBill, ...bills]); // latest top me
    setShowModal(false);
    setForm({ title: "", category: "", date: "", amount: "" });
  };

  // ✅ Delete bill
  const deleteBill = async (id) => {
    await fetch(`/api/bills/${id}`, { method: "DELETE" });
    setBills(bills.filter((b) => b._id !== id));
  };

  return (
    <div className="w-full flex justify-center items-start mt-8 px-4">
      <div className="w-full max-w-4xl">
        {/* Heading */}
        <div className="flex justify-between items-center mb-5 flex-wrap">
          <h2 className="text-lg sm:text-xl font-semibold text-gray-700">
            Upcoming Bills
          </h2>
          <Button onClick={() => setShowModal(true)} className="bg-teal-600 text-white">
            Add Your Upcoming Bills
          </Button>
        </div>

        {/* Table */}
        <Card className="p-4 rounded-xl shadow bg-white w-full">
          <div className="hidden md:block overflow-x-auto">
            <table className="w-full text-sm">
              <thead>
                <tr className="text-gray-500 border-b">
                  <th className="py-2 text-left">Due Date</th>
                  <th className="py-2 text-left">Category</th>
                  <th className="py-2 text-left">Title</th>
                  <th className="py-2 text-right">Amount</th>
                  <th className="py-2 text-center">Pay Now</th>
                  <th className="py-2 text-center">Delete</th>
                </tr>
              </thead>
              <tbody>
                {bills.map((bill) => (
                  <tr key={bill._id} className="border-b last:border-0">
                    <td className="py-3">{bill.date}</td>
                    <td className="py-3">{bill.category}</td>
                    <td className="py-3 font-medium text-gray-800">{bill.title}</td>
                    <td className="py-3 font-semibold text-gray-900 text-right">
                      ${bill.amount}
                    </td>
                    <td className="py-3 text-center">
                      <Button size="sm" className="bg-green-500 text-white">
                        Pay Now
                      </Button>
                    </td>
                    <td className="py-3 text-center">
                      <Button
                        size="sm"
                        variant="destructive"
                        onClick={() => deleteBill(bill._id)}
                      >
                        Delete
                      </Button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </Card>

        {/* Load More */}
        {bills.length > 5 && (
          <div className="flex justify-center mt-4">
            <Button variant="outline">Load More</Button>
          </div>
        )}

        {/* Modal */}
        <Dialog open={showModal} onOpenChange={setShowModal}>
          <DialogContent className="max-w-md">
            <DialogHeader>
              <DialogTitle>Add New Bill</DialogTitle>
            </DialogHeader>
            <div className="space-y-3">
              {/* Category Dropdown */}
              <select
                value={form.category}
                onChange={(e) => setForm({ ...form, category: e.target.value })}
                className="w-full p-2 border rounded-lg"
              >
                <option value="">Select Category</option>
                <option value="Credit Card">Credit Card</option>
                <option value="Figma">Figma</option>
                <option value="Adobe">Adobe</option>
                <option value="Netflix">Netflix</option>
              </select>

              {/* Title */}
              <input
                type="text"
                placeholder="Title"
                value={form.title}
                onChange={(e) => setForm({ ...form, title: e.target.value })}
                className="w-full p-2 border rounded-lg"
              />

              {/* Amount */}
              <input
                type="number"
                placeholder="Amount"
                value={form.amount}
                onChange={(e) => setForm({ ...form, amount: e.target.value })}
                className="w-full p-2 border rounded-lg"
              />

              {/* Date Picker */}
              <div className="flex items-center gap-2">
                <Calendar className="text-gray-600" />
                <input
                  type="date"
                  value={form.date}
                  onChange={(e) => setForm({ ...form, date: e.target.value })}
                  className="w-full p-2 border rounded-lg"
                />
              </div>

              {/* Add Button */}
              <Button className="w-full bg-teal-600 text-white" onClick={addBill}>
                Add Bill
              </Button>
            </div>
          </DialogContent>
        </Dialog>
      </div>
    </div>
  );
}
